<template>
    <div class="home">
      <div v-if="IS_CONNECTED">
        <div class="home__inputs">
          <div class="input-group mb-3">
            <span class="input-group-text" id="inputGroup-sizing-default">Oracle Label</span>
            <input v-model="oracleLabel"  placeholder="Oracle Label" type="text" class="form-control" aria-describedby="inputGroup-sizing-default">
          </div>
          <button @click="handleSetLabel" type="button" class="btn btn-primary">Set Label</button>
        </div>
      </div>
      <div v-else class="mx-auto">
        <button @click="CONNECT_METAMASK" type="button" class="btn btn-warning btn-lg">Connect Wallet</button>
      </div>
      <Window/>
      <Loader v-if="LOADER" />
    </div>
  </template>
  
  <script>
  import { mapGetters, mapActions } from 'vuex'
  import Loader from "../components/Loader";
  import Window from "../components/Window.vue";
  
  export default {
    data() {
          return {
            oracleLabel: ''
          }
      },
    name: 'OracleLabel',
    components: {
      Loader,
      Window
    },
    computed: {
      ...mapGetters(['IS_CONNECTED','LOADER'])
    },
    methods: {
      ...mapActions(['CONNECT_METAMASK','SET_ORACLE_LABEL']),
      handleSetLabel() {
        if (!this.oracleLabel) {
          this.$vToastify.info('Fill input', 'Validation warning')
        } else {
          this.SET_ORACLE_LABEL(this.oracleLabel);
        }
      }
    },
  }
  </script>
  
  <style scoped>
  .home__inputs {
    width: 900px;
    margin: 0 auto;
  }
  </style>
  